.cta {
  height: 50px;
  width: 180px;
  color: white;
  z-index: 1;
  transition: 1.2s;
  background: linear-gradient(to right bottom, #4776E6, #8E54E9);
}

.number {
  height: 50px;
  width: 180px;
  /* background-image: ("back.png"); */
  background-size: cover;
  color: white;
  box-shadow: 3px 3px 30px #aaa;
}

.cta:hover {
  transform: translateX(-80%);
  transition: 1.2s;
}




